$color: grey;
.todoItem {
    display: flex;
    border: 1px solid $color;
    list-style-type: none;
    padding: 5px;
    .itemText {
        color: blue;
    }
    &Done {
        &>span {
            text-decoration: line-through;
        }
    }
    &Important {
        font-weight: bold;
    }
    button {
        &:first-of-type {
            margin-left: auto;
        }
        &:last-of-type {
            background: tomato;
        }
    }
}
